import React, { useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { DragDropContext, Droppable } from 'react-beautiful-dnd'
import { Container, ListGroup, Jumbotron } from 'react-bootstrap'

import Routine from './routine/Routine'

import { handleDragEnd } from '../utils'

function Routines({ actions, routines }) {
  const history = useHistory()

  useEffect(() => {
    actions.requestRoutinesData()
  }, [actions])

  const setRoutinesCallback = (routines) => {
    actions.orderRoutines(routines)
  }

  const onRoutineClick = (routineId) => {
    history.push(process.env.PUBLIC_URL + '/routine/' + routineId)
  }

  const onDragEnd = (result) => {
    handleDragEnd(result, routines, setRoutinesCallback)
  }

  return (
    <Container>
      <Jumbotron fluid>
        <Container>
          <h2>Start your exercise by selecting one of the routines!</h2>
          <p>
            Select one of the available routines and start your exercise! More
            routines will be available soonish
          </p>
        </Container>
      </Jumbotron>
      <DragDropContext onDragEnd={onDragEnd}>
        <Droppable droppableId="list">
          {(provided) => (
            <ListGroup ref={provided.innerRef} {...provided.droppableProps}>
              {routines.map((routine, index) => (
                <Routine
                  routine={routine}
                  index={index}
                  key={routine.id}
                  selectRoutine={onRoutineClick}
                />
              ))}
              {provided.placeholder}
            </ListGroup>
          )}
        </Droppable>
      </DragDropContext>
    </Container>
  )
}

export default Routines
