import React from 'react'
import { useHistory } from 'react-router-dom'

import { Container, Jumbotron, Button } from 'react-bootstrap'

import { getAudioManager } from '../utils'
import ExerciseList from './exercise/ExerciseList'

function Exercises(props) {
  const { exercises, routineId, actions } = props

  const history = useHistory()

  const startExercise = () => {
    // safari hack to provide audio for the rest of the app
    getAudioManager()

    const exercise = exercises[0]

    if (exercise) {
      actions.startExercise(exercise)
      history.push(
        process.env.PUBLIC_URL + `/routine/${routineId}/exercise/${exercise.id}`
      )
    }
  }

  // const goBackCallback = () => {
  //   history.goBack()
  // }

  return (
    <Container>
      <Jumbotron fluid>
        <Container>
          <h2>Start your exercise by pressing start button!</h2>
          <p>
            You can change the order of exercises by dragging them around until
            you are completely happy with the new order
          </p>
          <p>
            <Button variant="primary" onClick={startExercise}>
              Start routine!
            </Button>
          </p>
        </Container>
      </Jumbotron>
      <ExerciseList {...{ actions, exercises, routineId }} />
    </Container>
  )
}

export default Exercises
